import { Component, OnInit } from '@angular/core';
import { DemoLinkService } from 'src/app/services/demo-link.service';
import { GirdDataService } from 'src/app/services/gird-data.service';

@Component({
  selector: 'app-components-page-container',
  templateUrl: './components-page-container.component.html',
  styleUrls: ['./components-page-container.component.scss']
})
export class ComponentsPageContainerComponent implements OnInit {

  private subHeaderContent:any;
  private componentsInfo:any;
  private showCards:any;
  private componentData:any;

//   @HostListener('window:scroll', ['$event']) getScrollHeight(event) {
//     console.log(window.pageYOffset, event);
//  }

  constructor(private _dl :DemoLinkService,private _ds:GirdDataService) { }

  ngOnInit() {

    this.showCards='cards';


    this.subHeaderContent = {
      icon: 'fa fa-cubes',
      info: [
        'Easy to use prebuilt components',
        'Install using NPM',
        'Control the look and feel as you wish',
        'Get hard to implement functionalities out of the box'
      ],
      showIconBig: true,
    }

    this.componentsInfo= this._ds.getDataForCard().JsCards;


  }
  onInconClick(){
    this.showCards = 'cards';
  }

  onCardClick(item){ 

    if(item.disabled)return;

    this.showCards = item.label;

    let tempLink ;
    let showYt;
    
    switch(this.showCards) {
      case "Sd-Easy-Notify":
        tempLink =this._dl.getLinks().EASY_NOTIFY_DEMO;
        showYt=true;
        break;
      case "Sd-Easy-Stepper":
        tempLink=this._dl.getLinks().EASY_STEPPER_DEMO ;
        showYt=true;
        break;
      default:
        tempLink="su9EVeHqizY"
    }

    this.componentData= { icon: 'fa fa-cubes',info:[item.label],videoLink:tempLink,showDemoSmall:showYt,demoLinkText:" Demo",showIconSmall: true,} 

 
  }

}
