import { Component, OnInit } from '@angular/core';
import { GirdDataService } from '../../services/gird-data.service'

@Component({
  selector: 'app-sd-easy-notify',
  templateUrl: './sd-easy-notify.component.html',
  styleUrls: ['./sd-easy-notify.component.scss']
})
export class SdEasyNotifyComponent implements OnInit {

  private installMsg:any;
  private importMsg:any;
  private useMsg:any;
  private contentConfig:any;
  private contentDisplay:any;
  private contentLook:any;

  private inputContent:any;
  private inputConfig:any;
  private inputLook:any;


  

  constructor(private _ds:GirdDataService) { }


  ngOnInit() {

    this.inputConfig=this._ds.getDataForNotify().config;
    this.inputContent=this._ds.getDataForNotify().content;
  

    this.installMsg = `
    npm install sd-easy-notify`;
    this.importMsg = `
    import {SdEasyNotifyModule} from "node_modules/sd-easy-notify"`;
    this.useMsg = `
    <sd-easy-notify 
    [content]='content' 
    [config]='config'  
    [look]='look' 
    (onActionState_e)="showState($event)"> 
    </sd-easy-notify>`;

    this.contentConfig = `
    config:{ 
      time:5000,//set timeout for individual notification
      position:'right',//[left , right],
      animation:true,//slide animation will be applied
      close:true //show close button,
      maxWidth:'400px' // default will be 100%
    }
    `

    this.contentDisplay = `

    /** Keep pushing new element to the array to POP new notification **/

    content: [
    {
      notice: "Lorem ipsum dolor sit amet",

      description: "Lorem ipsum dolor sit amet, 
      consectetur adipiscing elit. Mauris iaculis tincidunt porttitor. 
      Phasellus et dignissim eros, mattis ullamcorper purus. 
      Donec convallis est eu sem imperdiet viverra. 
      Nunc eget convallis ipsum. Duis mauris lacus, 
      pretium id egestas vitae.",

      type:'warn' //[warn,info,danger]
    }
  ]
    `;
     
    this.contentLook = `
    look : {
      sd__notifyWrapperClass:"",
      sd__notifyWarnClass:"",
      sd__notifyInfoClass:"",
      sd__notifyDangerClass:"",
      sd__notifyDescriptionClass:"" 
  }
    `

    
  }

  pushNewNotice(){
    this.inputContent = [
      {
        notice: "WOW ! I Got Added",
  
        description: "So, This is how it works, Keep pushing new notices to the array and let the component do the work.",
  
        type:'info'
      },...this.inputContent]
  }

}
