import { Component, OnInit } from '@angular/core';
import { GirdDataService } from '../../services/gird-data.service'

@Component({
  selector: 'app-sd-easy-stepper',
  templateUrl: './sd-easy-stepper.component.html',
  styleUrls: ['./sd-easy-stepper.component.scss']
})
export class SdEasyStepperComponent implements OnInit {
  private installMsg:any;
  private importMsg:any;
  private useMsg:any;
  private contentConfig:any;
  private contentDisplay:any;
  private contentLook:any;

  private inputContent:any;
  private inputConfig:any;
  private inputLook:any;


  

  constructor(private _ds:GirdDataService) { }


  ngOnInit() {

    this.inputConfig=this._ds.getDataForStepper().config;
    this.inputContent=this._ds.getDataForStepper().content;
    this.inputLook=this._ds.getDataForStepper().look;

    this.installMsg = `
    npm install sd-easy-stepper`;
    this.importMsg = `
    import {SdEasyStepperModule} from "node_modules/sd-easy-stepper"`;
    this.useMsg = `
    <sd-easy-stepper 
    [content]='content' 
    [config]='config' 
    [look]='look' 
    (onActionState_e)="showState($event)"> 
    </sd-easy-stepper>`;

    this.contentConfig = `
    config:{
      showStatus:true,//[true,false]
      statusType:'square',//[square,circle]
      showStepNumber:true,//[true,false]
      stepStyle:'none',//[arrow,box,none,merge,rounded]
      theme:'dark', //[light,dark]
      link:'strike', //[strike,arrowHead,none] 
      effect:true //[true,false] //creates ripple effect
      preSelectedStep:1 //step number that will be preselected
    }
    `

    this.contentDisplay = `
    content : [
      {
        step:1,
        label:"step 1 text",
        status:'' //[completed,locked,disabled,error,warnning,info]
      },
      {
        step:2,
        label:"step 2 text",
        status:'error'
      },
      {
        step:3,
        label:"step 3 text",
        status:''
      },
      {
        step:4,
        label:"step 4 text",
        status:'disabled'
      } 
    ],
    
    `;
     
    this.contentLook = `
    look:{
      sd__stepClass:'blue' <== .blue{background-color:blue}
    }
    `
 
  }

  addToConfig(prop,val){ 
    this.inputConfig = {...this.inputConfig,[prop]:val}
  }

}
